table {
    border-top: solid;
    border-bottom: solid;
    border-color: grey;
    border-width: 1px;
    margin: auto;
    margin-top: 30px;
    max-width: 1150px;
    overflow-x: auto;
}

td, th {
    padding: 0px 40px 0px 40px;
    /* padding: auto; */
}

tbody:nth-child(even) {
    background-color: #f2f2f2;
}

tr {
    height: 30px;
}

.downArrow {
    width: 0;
    height: 10;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 10px solid #555;
    margin-top: 50px;
    position: relative;
    top: 16px;
    left: 10px;
}